@use "base/variables" as v;
@use "base/colours" as c;
@use "base/mixins" as m;

.bar {
  display: flex;
  justify-content: space-between;
}
.nav {
  .nav-list {
    display: flex;
    align-items: center;
    .nav-list__item {
      margin-right: v.$margin_elemt;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
.nav--header {
  display: none;
  @include m.tablet {
    display: flex;
  }
}
.icon-hamburguer {
  display: flex;
  @include m.tablet {
    display: none;
  }
}
.follow {
  .follow__icon {
    margin-right: v.$margin_elemt;
    &:last-of-type {
      margin-right: 0;
    }
  }
}
